import React, { useState, useEffect, useCallback } from 'react';
import Gallery from "react-photo-gallery";
import Tabletop from "tabletop";
import logo from './logo.svg'
import './App.css';

function App() {

  const [data, setData] = useState([]);
  console.log(data)
  useEffect(() => {
    Tabletop.init({
      key: "17ZLB9Kbu4O9IF4g464NayCzDur4gv9jPlwQDIqEIwgo",
      simpleSheet: true
    })
      .then((data) => setData(data.reverse()))
      .catch((err) => console.warn(err));
  }, []);

  const openPhoto = useCallback((event, { photo }) => {
    window.open(photo.link, "_blank")
  }, []);

  return (
    <div>
      <div style={{ width: "60%", margin: "auto" }}>
        <h1 style={{ paddingTop: "2.5%", paddingBottom: "2.5%" }}> Spectator's Instagram Articles List </h1>
        <Gallery photos={data} onClick={openPhoto} />
      </div>
      <div style={{ textAlign: "center", padding: "3%" }}>
        Made with &hearts; by Spectator's Newsroom Development Team
      </div>
    </div>
  );
}

export default App;
